<template>
  <div class="form-control-wrap">
    <label class="d-block">
      <span class="form-control-label">{{title}}</span>
      <span class="gradient-border">
        <textarea  v-if="type==='textarea'" class="form-control" :placeholder="placeholder"  v-model="changeInput"></textarea>
        <input v-else :type="type" class="form-control" :placeholder="placeholder"  v-model="changeInput">
      </span>
    </label>
    <div class="form-control-clear" v-if="changeInput" @click="resetInput" ><Close/></div>
  </div>
</template>

<script>
import Close from '../icons/Close.vue'
export default {
  name: "FormControl",
  components: {
    Close,
  },
  props: ['title','type','placeholder'],
  data() {
    return{
      changeInput: undefined,
    }

  },
  methods: {
    resetInput() {
      this.changeInput = undefined;
    },
  },
}
</script>

